  .list-actions {padding: 4px 0px 4px;}
  .dashbord-widget i {float: right;font-size: 35px;color: #8b9197;}
  .bg-orange {background-color: rgb(254, 104, 71);}
  .expense-card .custom-control {  top: 0px;  left: 4px;}
</style>