<template>
    <div>
    <div class="left-menu-tab new_tab">
        <!-- {{ SearchFields }} -->
        <leftsearch :header="HeaderText"
                    v-bind:fields="SearchFields"
                    v-on:SetLeftSearch="SetActiveKey"
                    :modulename="ModuleName"
                    for="ExpenseReport"> 
        </leftsearch>
       
    </div>
     <div class="right-content">
      <div class="col-md-12 p-0">
            <div class="theme-primary partition p-actions-expand" style="overflow: visible;">
                <span class="p-name text-white" id="spnreporttext"></span>
                <span class="p-actions float-right">
                    <a href="javascript:;" id="ancshowmorefilter" class="p-action-btn text-white" style="display:none;" title="@Resources.Show @Resources.More @Resources.Filter"><i class="fa fa-filter" aria-hidden="true"></i><br>@Resources.Show @Resources.More @Resources.Filter</a>
                </span>
                <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                </span>
            </div>
        </div>
     <div class="col-md-12 p-0">
            <div class="tab-content" id="myTabContent">
    <div class="expand-list">
        <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
            <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
        </a>
    </div>
     <div id="ExpenseOverLimitReport" class="tab-pane active" :class="{'active show':activeKey==1}" v-if="activeKey==1">
    <div id="listing">        
        <div class="col-md-12 p-0">
            <form id="frmReport" method="post" target="iReport" asp-controller="ReportCommon" asp-action="ReportMain">
                <input type="hidden" id="Projects" name="projectIds" value="" />
                <input type="hidden" id="Categorys" name="categoryIds" value="" />
                <input type="hidden" id="reportName" name="reportName" value="expenseoverlimit" />
                <input type="hidden" id="hdnmodulename" name="moduleName" value="Expense" />
                <div class="row">
                    <div class="col-lg-4">
                        <div class="form-group">
                            <label>{{ $t('Project') }}:</label>
                            <Multiselect v-model="projectId" :multiple="true"  placeholder="Choose your Project" :close-on-select="false" :preselect-first="true"
                                :min-chars="1" :resolve-on-load="false" :delay="0" :preserve-search="true"
                                :options="projects" track-by="value" :object="true"   label="label"> 
                                <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template></Multiselect>
                                                           
                        </div>
                    </div>
                    <div class="col-lg-4">
                        <div class="form-group">
                            <label>{{ $t('Category') }}:</label>
                            <Multiselect v-model="categoryId" :multiple="true"  placeholder="Choose your Category" :close-on-select="false" :preselect-first="true"
                                 :min-chars="1" :resolve-on-load="false" :delay="0"  :preserve-search="true"
                                :options="categories" track-by="value"  label="label" :object="true" >
                                <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template></Multiselect>
                                
                        </div>
                    </div>
                    <div class="col-lg-4">
                        <div class="form-group">
                            <label>{{ $t('OverLimit') }}:</label>
                            <div class="custom-control custom-checkbox custom-control-inline w-100">
                                <input type="checkbox" class="custom-control-input dynamic" id="chkoverlimit"
                                    name="chkoverlimit" value="1" :checked="isexpenseOverLimit">
                                <label class="custom-control-label" for="chkoverlimit"></label>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-4">
                        <div class="form-group">
                            <label>{{ $t('Date') }}(M/D/YYYY) :</label>
                            <div class="row">
                                <div class="col-lg-6">
                                    <div class="input-group">
                                        <v-date-picker  :minute-increment="5" class="input-group" 
                                        v-model="dtFrom" :attributes='attrs'>
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control date' : true}" :value="inputValue"
                                                    v-on="inputEvents"  placeholder="M/D/YYYY"  />
                                                <div @click="togglePopover()" class="input-group-append">
                                                    <span class="input-group-text">
                                                        <em class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>
                                    </div>
                                </div>
                                <div class="col-lg-6">
                                   
                                    <div class="input-group">
                                        <v-date-picker   :minute-increment="5" class="input-group" 
                                        v-model="dtTO" :attributes='attrs'>
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control date' : true}" :value="inputValue"
                                                    v-on="inputEvents" placeholder="M/D/YYYY" />
                                                <div @click="togglePopover()" class="input-group-append">
                                                    <span class="input-group-text">
                                                        <em class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row flex-row-reverse mt-2">
                    <div class="col-lg-6 text-right">
                        <a href="javascript:;" id="aSearch" :title="$t('Search')" name="aSearch"
                            class="btn btn-success formbtn text-white mr-1" @click="onSearch"><i
                                class="fa fa-search pr-2"></i>{{ $t("Search") }}</a>
                        <a href="javascript:;" :title="$t('Clear')" id="aClear"
                            class="btn btn-danger formbtn widthhalf btnCancel" @click="onClear"><i
                                class="fa fa-close pr-2"></i>{{ $t("Clear") }}</a>
                    </div>
                </div>
            </form>
        </div>
        <div class="row placeholder">
            <div class="col-md-12 p-0">
                <ReportViewer v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
                 <!-- <div id="dvMainReportPanel" style="box-sizing: border-box; -webkit-overflow-scrolling:touch!important; text-align: center !important; width: 100% !important;display:none;">
                   <iframe src="" style="width:100%;min-height:80vh;margin-bottom: 0%;height: auto;min-height: 650px;"
                        id="iReport" name="iReport" scrolling="no" frameborder="0">
                        
                    </iframe> 
                    
                </div>-->
            </div>
        </div>
    </div>
   </div>
    <div id="ExpenseReport" class="tab-pane" :class="{'active show':activeKey==2}" v-if="activeKey==2">
        <ExpenseReport></ExpenseReport>
        
   </div>
   <div id="reimbursementreport" class="tab-pane" :class="{'active show':activeKey==3}" v-if="activeKey==3">
        <ReimbursementReport></ReimbursementReport>
        
   </div>
</div>
   </div>
</div>
</div>
</template>
<script>
import Schema from "./ExpenseReportScheam";
import DataService from "@/services/DataService";
import ReportViewer from './ReportViewer.vue'
import leftsearch from '@/components/Common/left-search.vue';
import ExpenseReport from './ExpenseReport.vue'
import ReimbursementReport from "./ReimbursementReport.vue";
export default {
    components:{
        leftsearch,
        ReportViewer,
        ExpenseReport,
        ReimbursementReport
    },
    data: function () {
        return {
            activeKey:1,
            HeaderText:'Expense Reports',
            categories:[],
            projects:[],
            SearchFields:Schema.leftsearchSchema,
            ModuleName:'Expense',
            dtFrom: "",
            dtTO:"",
            isexpenseOverLimit:true,
            projectId:null,
            categoryId:null,
            attrs: [
                {
                    key: 'today',
                    bar: true,
                    dates: new Date(),
                },
            ],
            reportPath:null,
            parameter:[]
        }
    },
    created: async function(){
        console.log(Schema)
        console.log(this.SearchFields);
        await this.GetProjectByCompanyIdAndUserId();
        await this.GetAllCategoriesByCompanyId();
    },
    methods: {
        SetActiveKey:function(activeKey){
            debugger;
this.activeKey=activeKey;
        },
        onSearch: function () {
         var vm=this;
            var reportModel={
                reportName:'expenseoverlimit',
                moduleName:'Expense',
                projectIds:vm.projectId.toString(),
                categoryIds:vm.categoryId.toString(),
                dateFrom:vm.dtFrom,
                dateTo:vm.$options.filters.formatDate(vm.dtTO),
                expenseOverLimit:vm.isexpenseOverLimit
            }  
            var objectPostString = JSON.stringify(reportModel);     
            DataService.ReportMain(objectPostString).then(response => {                  
                     vm.reportPath=response.data.reportpath;
                     vm.parameter=response.data.parameters;
            });
        },
        onClear: function () {
            $('#dvMainReportPanel').css('display', 'none');
            $('#Project').multiselect('uncheckAll');
            $('#Category').multiselect('uncheckAll');
            $('#dtFrom').val('');
            $('#dtTo').val('');
            $("#Projects").val('');
            $("#Categorys").val('');
            $('#iReport').attr('src', '');
            $('#chkoverlimit').removeAttr('checked');

        },
        GetProjectByCompanyIdAndUserId:async function() {          
            var vm = this;
           await DataService.GetProjectByCompanyIdAndUserId().then(function (response) {
            vm.projects = response.data.map((item) => {
            return { value: item.projectId, label: item.projectName };
          }); 
            });
        },
        GetAllCategoriesByCompanyId:async function() {
         var vm = this;
           await DataService.GetAllCategoriesByCompanyId().then(function (response) {
                vm.categories =  response.data.map((item) => {
            return { value: item.expensE_CATEGORY_ID, label: item.categorY_NAME };
          });
        });
        },
        showCheckboxes() {
            var checkboxes = document.getElementById("checkboxes");
            if (!expanded) {
                checkboxes.style.display = "block";
                expanded = true;
            } else {
                checkboxes.style.display = "none";
                expanded = false;
            }
        }
    }
}
</script>