    function bindleftCommonSearchdropdown(instance,modulename, filtername) {
       var vm = instance;
       let params = {};
       params["moduleName"] = modulename; 
       params["type"] = filtername      
       ExpenseDataServices.GetDDLData(params)
           .then(function (response) {
           var json = response.data;                
               if (json != null) { 
                   if(filtername == "EXPENSE_PROJECT"){
                       vm.leftSearchFields[0].listOptions = json.DATA;
                        vm.ExpenseProjectList = json.DATA;  
                        return vm.ExpenseProjectList;
                   }
                   else{
                       vm.leftSearchFields[1].listOptions = json.DATA;
                        vm.ExpenseStatusList = json.DATA;
                        return vm.ExpenseStatusList;
                   }
                                                     
               }
           });
    };
    export default { buttons, leftsearchSchema,listActions,bindleftCommonSearchdropdown};