
<template>
    
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('ManageExpense') }}</span>
                        <span class="float-right">
                            <a href="javascript:;" class="p-action-btn text-white" title="Page Tour"
                                @click="setTourGuideSteps(0)">
                                <em class="fa fa-road" aria-hidden="true"></em>
                                <br />{{ $t('PageTour') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <dynamic-form lang="en" ref="form" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
            <ExpenseAttachents v-if="showFileModal" @CloseExpenseAttachments='CloseExpenseAttachments'
            v-bind:expenseid="expid" v-bind:code="statuscode"></ExpenseAttachents>
            <ModelTrip v-if="ShowTripModel" @CloseExpenseTrip='CloseExpenseTrip' @GetTripNameDDL="GetTripNameDDL"></ModelTrip>
            <ModelTag v-if="ShowTagModel" @CloseExpenseTag='CloseExpenseTag'></ModelTag>
        </div>
    </section>
</template>

<script>
import ExpenseDataService from "@/services/DataService";
import ModelTrip from "../../../components/Common/Modal/modalTrip.vue"
import ModelTag from "../../../components/Common/Modal/ModelTag.vue"
import ExpenseAttachents from "../../../components/Common/Modal/ExpenseAttachments.vue"
export default {
    components: {        
        ModelTag,
        ModelTrip
    },
    data() {
        return {
            isLoading: false,
            showFileModal: false,
            ShowTripModel: false,
            ShowTagModel: false,
            CategoryDataddl: [],
            TaskDataddl: [],
            CurrencyNameddl: [],
            TagDataddl: [],
            usertype: null,
            ExpenseId: 0,
            ProjectData: '',
            ExpAmount:'',
            IsSendForApproval: null,
            file: [],
            fileCount: '',
            expid: '',
            txtAmountCode: '',
            Currency: null,
            chkStatus: '',
            TripData: [],
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [                                
                        {
                            astype: "TextField",
                            label: this.$t('ExpenseTitletext'),
                            name: "EXPENSE_TITLE",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },                            
                        },
                        {
                            astype: "DateField",
                            label: this.$t('ExpenseDate'),
                            name: "EXPENSE_DATE",
                            value: '',
                            placeholder: "M/D/YYYY",
                            validationRules: "required",
                            config: {
                                format: "MM/DD/YYYY"                                
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Header_Projects'),
                            name: "PROJECT_ID",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: { "required": true}
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Trip'),
                            name: "ExpenseTrip",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.OpenPopup
                            },
                            validationRules: ""
                        },
                        {
                            astype: "SelectField",
                            label: "Category",
                            name: "EXPENSE_CATEGORY_ID",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,                                
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "SelectField",
                            label: "Task",
                            name: "TASK_ID",
                            value: "",
                            placeholder: "",
                            config: { 
                                options: [],
                                showAddIcon: false,
                                // onChange: function (e, field) {
                                // },
                                // option_request_url:process.env.VUE_APP_API_URL+'/GetTaskByProject?projectId='+this.ProjectData,
                                // token:this.GetUserInfo.Token
                            },
                            validationRules: "",
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('Amount'),
                            name: "EXPENSE_AMOUNT",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 8 },
                            config: {
                                onkeypress: this.ExpenseAmount
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Currency'),
                            name: "currency_id",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.OnChangeCurrency
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Tag'),
                            name: "ExpenseTag",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.OpenPopup
                            },
                            validationRules: "",
                        },
                        
                    ],
                },
                {
                    group_name: "Attachment",
                    layoutType: "four",
                    Data: [
                        {
                            astype: "FileField",                        
                            showLabel:false,
                            name: "ExpenseAttechment",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize:5,
                                accept:[
                                    {
                                       fileType:'png',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'jpg',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'jpeg',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'pdf',
                                       iconClass:'fa-file-archive-o'
                                    }
                                ],
                                onChange: this.setFile,
                                showAddIcon: false,
                                onAddButtonClick: this.AddFileMore,
                                multiple:true
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('ExpenseDescription'),
                            name: "EXPENSE_DESCRIPTION",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            config: {
                                rows:5
                            }
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning",
                    text: "<i class='fa fa-save pr-2'></i>Save Of Draft",                    
                },
                {
                    type: "submit",
                    class: "btn btn-success saveSendforApproval",
                    text: "<i class='fa fa-paper-plane pr-2'></i>Save and Send for Approval"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick:this.onCancel
                }
            ],
            //expenseBaseUrl:process.env.VUE_APP_API_URL,
            expenseData:[]
        }

    },
    created:async function () {
        this.ExpenseId=this.$route.query.id?this.$route.query.id:0;
        this.usertype = this.GetUserInfo.UserType;
        this.Currency = this.GetUserInfo.Currency.replace('(', '').replace(')', ''); 
        await this.GetProjectDDL(); 
        await this.FetchData();
        await this.GetTripNameDDL();
        await this.GetCategoryNameDDL();
        await this.GetCurrencyNameDDL();
        await this.GetTagNameDDL();             
    }, 
    mounted(){
        setTimeout(() => {
            if (this.expenseData.length > 0) {
                debugger;
            this.$refs.form.SetFormValues(this.expenseData);
        }
        }, 3000);        
    },  
    methods: {
        OnChangeCurrency:function (e, field) {
            debugger;
       
        var start = $(field.name=="ExpenseCurrency").text().indexOf("(");
        var end = $(field.name=="ExpenseCurrency").text().indexOf(")", parseInt(start));
        var result = $(field.name=="ExpenseCurrency").text().substring(parseInt(start), parseInt(end) + 1);
        $(e.target.name=="ExpenseAmount").html(result);
        if ($(e.target.name=="ExpenseAmount").text() == "") {
          $(e.target.name=="ExpenseAmount").text(this.Currency);
        }                   
    },
        
        AddFileMore:function(e, field){
            var vm=this;            
            (vm.FormSchema)[1].Data.push({
                            astype: "FileField",                         
                            showLabel:false,
                            name: "ExpenseAttechment",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: (vm.FormSchema)[1].Data.length,
                            config: {
                                onChange: vm.setFile,
                                showAddIcon: true,
                                onAddButtonClick:vm.RemoveFile,
                                addIconBackground:'bg-danger',
                                addIcon:'fa fa-times'
                            }});
        },
        RemoveFile:function(e,field){            
            var vm =this;
            var checkindex =(vm.FormSchema)[1].Data.findIndex(x=>x.FieldIndex === field.FieldIndex);
            if(checkindex > -1){
                (vm.FormSchema)[1].Data.splice(checkindex,1);
            }
        },
        OpenPopup: function (e, field) {
            if (field.name == 'ExpenseTrip') {
                this.ShowTripModel = true;
            }
            else {
                this.ShowTagModel = true;
            }
        },
        onChangeDropdown:async function (e, field,dependentOptions) {
            this.ProjectData = e.target.value;
            if(dependentOptions){
                dependentOptions.forEach(optionInfo=>{
                    for (var name in optionInfo) {
                        this.schema.forEach(schema=>{
                schema.Data.forEach(field=>{
                    if(name==field.name){
                       field.config.options=optionInfo[name];
                    }
                })
              }) 
                    }
                })
            }
            await this.GetTaskDDL();
            await this.GetCurrencyCode();
        },      
        ViewFile: function () {
            this.showFileModal = true;
            this.expid = this.ExpenseId;
        },
        CloseExpenseAttachments: function () {
            this.showFileModal = false;
        },
        onCancel: function () {            
            this.$router.push({ name: 'Expense' });
        },
        FetchData:async function (e) { 
            var vm = this;
            const params = `ExpenseId=${vm.ExpenseId}`;
            await  ExpenseDataService.GetExpenseByExpenseId(params).then(async(response) => {
                if (response.data.length > 0) {
                    debugger
                    //vm.expenseData=response.data;
                    (vm.FormSchema)[0].Data[0].value = response.data[0].EXPENSE_TITLE;
                    (vm.FormSchema)[0].Data[1].value = response.data[0].EXPENSE_DATE;
                    (vm.FormSchema)[0].Data[2].value=response.data[0].PROJECT_ID;
                    (vm.FormSchema)[0].Data[3].value = response.data[0].TRIP_ID;
                    (vm.FormSchema)[0].Data[4].value = response.data[0].EXPENSE_CATEGORY_ID;
                    (vm.FormSchema)[0].Data[5].value=response.data[0].TASK_ID;
                    (vm.FormSchema)[0].Data[6].value = response.data[0].EXPENSE_AMOUNT;
                    (vm.FormSchema)[0].Data[7].value=response.data[0].currency_id;
                    (vm.FormSchema)[2].Data[0].value = response.data[0].EXPENSE_DESCRIPTION;
                    (vm.FormSchema)[1].Data[0].value = response.data[0].expense_file_path;
                    vm.fileCount = response.data[0].FILE_COUNT;
                    vm.chkStatus = response.data[0].STATUS_ID;
                    vm.ProjectData=response.data[0].PROJECT_ID;
                    await vm.GetTaskDDL();
                    await vm.GetCurrencyCode();
                }
            });
        },
        setFile: function (e) {
            console.log(e.target.files);
            for (let index = 0; index < e.target.files.length; index++) {
                const element = e.target.files[index];
                this.file.push(element)
            }
        },
        onSubmit: function (formfields, val) {
            debugger
            var vueObj = this;
            var datafields = formfields;
            var jsonobj = {
                ExpenseId: vueObj.ExpenseId,
                ExpenseTitle: datafields.EXPENSE_TITLE,
                TripId: datafields.ExpenseTrip,
                ProjectId: datafields.PROJECT_ID,
                ExpDesc: datafields.EXPENSE_DESCRIPTION,
                ExpAmount: datafields.EXPENSE_AMOUNT,
                CategoryId: datafields.EXPENSE_CATEGORY_ID,
                TaskId: datafields.TASK_ID,
                ExpAttachment: '',
                IsBulkUpload: 0,
                CurrencyId: datafields.currency_id,
                ExpDate: datafields.EXPENSE_DATE,
                IsSendForApproval: val.indexOf('Save and Send for Approval')>0? 1 : 0
            };
            const formData = new FormData();
            formData.append("postString", JSON.stringify(jsonobj));
            formData.append("files", vueObj.file);
            for (var i = 0; i < vueObj.file.length; i++) {
                let file = vueObj.file[i];
                formData.append('files[' + i + ']', file);
            }
            vueObj.isLoading=true;
            ExpenseDataService.SaveUpdateExpense(formData).then((response) => {
                debugger
                vueObj.isLoading=false;
                if (response.data.error == '100') {
                    if (val == 'A') {
                        vueObj.ShowAlert(vueObj.$t('ExpenseAdd', [vueObj.$t('Expense')]), "success", true, vueObj.$t("Alert"));
                        this.$router.push({ name: 'Expense' });
                    }
                    else {
                        vueObj.ShowAlert(vueObj.$t('ExpenseAdd', [vueObj.$t('Expense')]), "success", true, vueObj.$t("Alert"));
                        this.$router.push({ name: 'Expense' });
                    }
                }

            });
        },
        CloseExpenseTrip: function () {
            this.ShowTripModel = false;
        },
        CloseExpenseTag: function () {
            this.ShowTagModel = false;
        },
        GetProjectDDL: async function () {
            var vm = this;
            var params = `associated=no`
           await ExpenseDataService.ExpenseGetProjectListing(params).then(function (response) {
                (vm.FormSchema)[0].Data[2].config.options = response.data;
            });
        },
        GetTripNameDDL:async function () {
            var vm = this;
            (vm.FormSchema)[0].Data[3].config.options=[];
           await ExpenseDataService.GettTripListingddl().then(function (response) {
                vm.TripData = response.data;
                vm.TripData.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[3].config.options.push({
                        value: item.TRIP_ID,
                        name: item.TRIP_NAME
                    })
                })
            });
        },
        GetCategoryNameDDL:async function () {
            var vm = this;
           await ExpenseDataService.GettCategoryListingddl().then(function (response) {
                vm.CategoryDataddl = response.data;
                vm.CategoryDataddl.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[4].config.options.push({
                        value: item.EXPENSE_CATEGORY_ID,
                        name: item.CATEGORY_NAME
                    })
                })
            });
        },
        GetTaskDDL:async function () {
            var vm = this;
            var params = `projectId=${vm.ProjectData}`;
           await ExpenseDataService.GetTaskListingddl(params).then(function (response) {
                vm.TaskDataddl = response.data;
                vm.TaskDataddl.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[5].config.options.push({
                        value: item.task_id,
                        name: item.task_name
                    })
                })
            });
        },
        GetCurrencyNameDDL:async function () {
            var vm = this;
           await ExpenseDataService.GetCurrencyNameddl().then(function (response) {
                vm.CurrencyNameddl = response.data;
                vm.CurrencyNameddl.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[7].config.options.push({
                        value: item.CurrencyId,
                        name: item.Currency
                    })
                })
            });
        },
        GetTagNameDDL:async function () {
            var vm = this;
            var params = `moduleName=Expenses`;
          await  ExpenseDataService.GetTagNameddl(params).then(function (response) {
                vm.TagDataddl = response.data;
                vm.TagDataddl.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[8].config.options.push({
                        value: item.value,
                        name: item.text
                    })
                })
            });
        },
        GetCurrencyCode:async function () {
            var vm = this;
            var tempname = vm.$t('Amount')
            var params = `projectId=${vm.ProjectData}`;
           await ExpenseDataService.GetCurrencyCode(params).then(function (response) {                
                (vm.FormSchema)[0].Data[6].label = tempname + '('+`${response.data}` +')';
            });
        }
    }
}
</script>

<style>
#fileAttachment .file-upload{
    justify-content: flex-start !important                                                                                                                              ;
}
#fileAttachment .file-upload .file-upload__area {
    overflow: auto;
}
</style>                                                                