<template>
<div id="listing">
    <div class="col-md-12 p-0">
        <form id="frmReport">
            <input type="hidden" id="projectIds" name="projectIds" value="" />
            <input type="hidden" id="categoryIds" name="categoryIds" value="" />
            <input type="hidden" id="tripIds" name="tripIds" value="" />
            <input type="hidden" id="userIds" name="userIds" value="" />
            <input type="hidden" id="approverIds" name="approverIds" value="" />
            <input type="hidden" id="statusIds" name="statusIds" value="" />
            <input type="hidden" id="hdnmodulename" name="moduleName" value="Expense" />
            <input type="hidden" id="reportName" name="reportName" value="expensereport" />
            <div class="row">
                <div class="col-lg-4">
                    <div class="form-group" >
                            <label>{{ $t('Project') }}:</label>
                            <Multiselect v-model="projectId" :multiple="true"  placeholder="Choose your Project" :close-on-select="false" :preselect-first="true"
                            :preserve-search="true"  :min-chars="1" :resolve-on-load="false" :delay="0" 
                                :options="projects" track-by="value" :object="true"   label="label"> 
                                <template slot="selection" slot-scope="{ values, search, isOpen }">
                        <span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span>
                    </template></Multiselect>                          
                        </div>
                    </div>
             
                <div class="col-lg-4">
                    <div class="form-group">
                            <label>{{ $t('Category') }}:</label>
                            <Multiselect v-model="categoryId" :multiple="true"  placeholder="Choose your Category" :close-on-select="false" :preselect-first="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0"  :preserve-search="true" 
                                :options="categories" track-by="value"  label="label" :object="true" >
                                <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template></Multiselect>
                                
                        </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                            <label>{{ $t('Trip') }}:</label>
                            <Multiselect v-model="TripId" :multiple="true"  placeholder="Choose your Trip" :close-on-select="false" :preselect-first="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0"  :preserve-search="true"  
                                :options="trips" track-by="value"  label="label" :object="true" >
                                <template slot="selection" slot-scope="{ values, search, isOpen }">
                                <span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span>
                                </template>
                            </Multiselect>
                                
                        </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                            <label>{{ $t('User') }}:</label>
                            <Multiselect v-model="UserId" :multiple="true"  placeholder="Choose your User" :close-on-select="false" :preselect-first="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0"  :preserve-search="true"  
                                :options="users" track-by="USER_ID"  label="USER_NAME" :object="true" >
                                <template slot="selection" slot-scope="{ values, search, isOpen }">
                                <span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span>
                                </template>
                            </Multiselect>                                
                        </div>
                </div>
               
                    <div class="col-lg-4">
                        <div class="form-group" v-if="UserType=='CA'">
                            <label>Approver:</label>
                            <Multiselect v-model="approverId" :multiple="true"  placeholder="Choose your Approver" :close-on-select="false" :preselect-first="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0"  :preserve-search="true" 
                                :options="approve" track-by="value"  label="name" :object="true" >
                                <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template></Multiselect>
                          </div>
                    </div>
        
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>Status:</label>
                        <Multiselect v-model="StatusId" :multiple="true"  placeholder="Choose your Status" :close-on-select="false" :preselect-first="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="status" track-by="value"  label="label" :object="true" >
                                <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template></Multiselect>
                       </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>Title:</label>
                        <input type="text" v-model="ExpenseTitle" name="ExpenseTitle" id="ExpenseTitle"  class = "form-control" />
                        <span data-valmsg-for="ExpenseTitle" data-valmsg-replace="true" class="valid-feedback">
                        </span>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>Date:</label>
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="input-group mb-2">
                                   
                                        <v-date-picker  :minute-increment="5" class="input-group" 
                                        v-model="startDate" :attributes='attrs'>
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control date' : true}" :value="inputValue"
                                                    v-on="inputEvents"  placeholder="From"  />
                                                <div @click="togglePopover()" class="input-group-append">
                                                    <span class="input-group-text">
                                                        <em class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>
                                   
                                </div>
                            </div>
                            <div class="col-lg-6">
                                <div class="input-group mb-2">
                                    <v-date-picker  :minute-increment="5" class="input-group" 
                                        v-model="endDate" :attributes='attrs'>
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control date' : true}" :value="inputValue"
                                                    v-on="inputEvents"  placeholder="To"  />
                                                <div @click="togglePopover()" class="input-group-append">
                                                    <span class="input-group-text">
                                                        <em class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>
                                   
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>GroupBy:</label>
                        <div class="pl-0 border-0">
                            <div class="custom-control custom-radio d-inline-block">
                                <input data-change-id="no" class="form-check-input" name="groupBy" id="Project" checked="checked" type="radio" value="Project">
                                <label class="form-check-label" for="Project">
                               {{ $t('Project')}}
                                </label>
                            </div>
                            <div class="custom-control custom-radio d-inline-block">
                                <input data-change-id="no" class="form-check-input" name="groupBy" id="Category" type="radio" value="Category">
                                <label class="form-check-label" for="Category">
                                    {{ $t('Category')}}
                                </label>
                            </div>
                            <div class="custom-control custom-radio d-inline-block">
                                <input data-change-id="no" class="form-check-input" name="groupBy" id="Trip" type="radio" value="Trip">
                                <label class="form-check-label" for="Trip">
                                    {{ $t('Trip')}}
                                </label>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse mt-2">
                <div class="col-lg-6 text-right">
                    <a href="javascript:;" id="aSearch" :title="$t('Search')" name="aSearch" @click="onSearch"
                    class="btn btn-success formbtn text-white"><i class="fa fa-search pr-2"></i>Search</a>
                    <a href="javascript:;" :title="$t('Clear')" id="aClear"  @click="onClear"
                    class="btn btn-danger formbtn widthhalf btnCancel"><i class="fa fa-close pr-2"></i>Clear</a>
                </div>
            </div>
        </form>
    
    </div>
    <div class="row placeholder">
        <div class="col-md-12 p-0">
            <ReportViewer v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
    </div>
</div>
</div>
</template>
<script>
import DataService from "@/services/DataService";
import ReportViewer from './ReportViewer.vue'
export default {
    components:{
        ReportViewer
    },
    data: function () {
        return {
            trips:[],
            categories:[],
            projects:[],
            status:[],
            users:[],
            ModuleName:'Expense',
            isexpenseReport:true,
            projectId:null,
            categoryId:null,
            TripId:null,
            UserId:null,
            approve:[],
            approverId:null,
            StatusId:null,
            startDate:null,
            endDate:null,
            ExpenseTitle:'',
            attrs: [
                {
                    key: 'today',
                    bar: true,
                    dates: new Date(),
                },
            ],
            reportPath:null,
            parameter:[]
        }
    },
    created:async function(){
       this.UserType=this.GetUserInfo.UserType;
       await this.GetProjectByCompanyIdAndUserId();
       await this.GetAllCategoriesByCompanyId();
       await this.GetStatusList("Expense", "en");        
       await this.ExpenseGetApproverListing();
       await this.GetAllTripsByCompanyId();
       await this.GetUsersByProjects();
    },
    methods: {
        CheckValid(el,cssClass) {
            var el1 = document.getElementById(el);          
            var spn =el1.getAttribute('name');
            el1.classList.remove("input-validation-error");          
            if (spn != undefined)
                $("span[data-valmsg-for='" + spn + "']").removeClass(cssClass).addClass("field-validation-valid").html(' ');            
            if (this.ExpenseTitle=='') {
                this.Sh(el1, Globalize.localize("InvalidInput"), cssClass);
                this.ShowAlert(vm.$t("InvalidInput"), "warning", true, this.$t("Alert"));
                return "no";
            }
            return '';
        },
        createStringArray(arr, prop) {
            var result = [];
            for (var i = 0; i < arr.length; i += 1) {
                result.push(arr[i][prop]);
            }
            return result.toString();
        },
        onSearch: function () {
            debugger;
         var vm=this;
        var validatonPass= vm.CheckValid('ExpenseTitle','field-validation-error');
       if(validatonPass=='no') return;
       var groupBy = $('input:radio[name=groupBy]:checked').val();
            var reportModel={
                reportName:'expensereport',
                moduleName:'Expense',
                projectIds:vm.createStringArray(vm.projectId,'value'),
                categoryIds:vm.createStringArray(vm.categoryId,'value'),
                userIds:vm.createStringArray(vm.UserId,'USER_ID'),
                StatusIds:vm.createStringArray(vm.StatusId,'value'),
                tripIds:vm.createStringArray(vm.TripId,'value'),
                approverIds:vm.createStringArray(vm.approverId,'value'),
                fDate:vm.startDate,               
                tDate:vm.endDate,
                expTitle:vm.ExpenseTitle,
                groupByVal:groupBy            
             }  
            var objectPostString = JSON.stringify(reportModel);     
            DataService.ReportMain(objectPostString).then(response => {                  
                     vm.reportPath=response.data.reportpath;
                     vm.parameter=response.data.parameters;
            });
        },
        onClear(){
            this.projectId=null;
            this.categoryId=null;
            this.TripId=null;
            this.UserId=null;
            this.approverId=null;
            this.StatusId=null;
            this.startDate=null;
            this.endDate=null;
            this.ExpenseTitle='';
        },
        GetProjectByCompanyIdAndUserId:async function() {          
          var vm = this;
         await DataService.GetProjectByCompanyIdAndUserId().then(function (response) {
          vm.projects = response.data.map((item) => {
          return { value: item.projectId, label: item.projectName };
        });          
          });
      },
      GetAllCategoriesByCompanyId:async function() {
       var vm = this;
         await DataService.GetAllCategoriesByCompanyId().then(function (response) {
              vm.categories =  response.data.map((item) => {
          return { value: item.expensE_CATEGORY_ID, label: item.categorY_NAME };
        });
      });
      },
      ExpenseGetApproverListing:async function(){
            let params = {};
            var vm=this;
            params["IsEncrptedId"] = '';
            params["moduleName"] = 'Expenses';
           await DataService.ExpenseGetUserListing(params).then(function (response) {
                vm.approve =  response.data;
            });
    },
    
    GetStatusList:async function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";           
           await DataService.GetStatusList(url).then(function (response) {  
                 vm.status = response.data.map((item) => {
          return { value: item.STATUS_ID, label: item.STATUS };
        });
                          
            });
    },
     async GetAllTripsByCompanyId() {
       var vm = this;
      await DataService.GettTripListingddl().then(function (response) {
              vm.trips =  response.data.map((item) => {
          return { value: item.TRIP_ID, label: item.TRIP_NAME };
        });
      });
      },
      async GetUsersByProjects() {
       var vm = this;
       var params=`projectIds=&moduleName=Project`;
      await DataService.GetUsersByProjectIds(params).then(function (response) {
              vm.users =  response.data;
      });
      }
    }

}
</script>