<template>

<div id="listing">
    <div class="col-md-12 p-0">
        <form id="frmReport" method="post" target="iReport" asp-controller="ReportCommon" asp-action="ReportMain">
            <input type="hidden" id="Projects" name="projectIds" value="" />
            <input type="hidden" id="Users" name="userIds" value="" />
            <input type="hidden" id="Approvers" name="approverIds" value="" />
            <input type="hidden" id="hdnmodulename" name="moduleName" value="Expense" />
            <input type="hidden" id="reportName" name="reportName" value="reimbursementreport" />
            <div class="row">
                <div class="col-lg-4">
                    <div class="form-group" >
                            <label>{{ $t('Project') }}:</label>
                            <Multiselect v-model="projectId" :multiple="true"  placeholder="Choose your Project" :close-on-select="false" :preselect-first="true"
                               :min-chars="1" :resolve-on-load="false" :delay="0" :preserve-search="true" 
                                :options="projects" track-by="value" :object="true"   label="label"> 
                                <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template></Multiselect>
                                                           
                        </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                            <label>Approver:</label>
                            <Multiselect v-model="approverId" :multiple="true"  :preselect-first="true"  placeholder="Choose your Approver" :close-on-select="false"
                            :preserve-search="true" :min-chars="1"
                                :options="approvers" track-by="value"  label="name" :object="true" >
                                <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template></Multiselect>
                          </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('User')}}:</label>
                        <Multiselect  v-model="UserId" :multiple="true"  placeholder="Choose your User" :close-on-select="false" :preselect-first="true"
                                :min-chars="1" :resolve-on-load="false" :delay="0"  :preserve-search="true" 
                                :options="users" track-by="name"  label="name" :object="true" >
                                <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template></Multiselect>
                      
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('GroupBy')}}:</label>
                        <div class="pl-0 border-0">
                            <div class="custom-control custom-radio d-inline-block">
                                <input data-change-id="no" class="form-check-input" name="groupBy" id="Company" checked="checked" type="radio" value="Company">
                                <label class="form-check-label" for="Company">
                                    {{$t('Company')}}
                                </label>
                            </div>
                            <div class="custom-control custom-radio d-inline-block">
                                <input data-change-id="no" class="form-check-input" name="groupBy" id="Client" type="radio" value="Client">
                                <label class="form-check-label" for="Client">
                                    {{$t('Client')}}
                                </label>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>Date:</label>
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="input-group mb-2">
                                   
                                    <v-date-picker  :minute-increment="5" class="input-group" 
                                        v-model="fromDate" :attributes='attrs'>
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control date' : true}" :value="inputValue"
                                                    v-on="inputEvents"  placeholder="From"  />
                                                <div @click="togglePopover()" class="input-group-append">
                                                    <span class="input-group-text">
                                                        <em class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>
                                </div>
                            </div>
                            <div class="col-lg-6">
                                <div class="input-group mb-2">
                                   
                                    <v-date-picker  :minute-increment="5" class="input-group" 
                                        v-model="toDate" :attributes='attrs'>
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control date' : true}" :value="inputValue"
                                                    v-on="inputEvents"  placeholder="To"  />
                                                <div @click="togglePopover()" class="input-group-append">
                                                    <span class="input-group-text">
                                                        <em class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse mt-2">
                <div class="col-lg-6 text-right">
                    <a href="javascript:;" id="aSearch" :title="$t('Search')" name="aSearch" class="btn btn-success formbtn text-white" @click="onSearch">
                        <i class="fa fa-search pr-2"></i>{{$t('Search')}}</a>
                    <a href="javascript:;" :title="$t('Clear')" id="aClear" class="btn btn-danger formbtn widthhalf btnCancel"
                    @click="onClear">
                        <i class="fa fa-close pr-2"></i>{{$t('Clear')}}</a>
                </div>
            </div>
        </form>
    </div>
    <div class="row placeholder">
        <div class="col-md-12 p-0">
            <ReportViewer v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
        </div>
    </div>
</div>
</template>
<script>
import DataService from "@/services/DataService";
import ReportViewer from './ReportViewer.vue'
export default {
    components:{
        ReportViewer
    },
    data: function () {
        return {
            projects:[],
            ModuleName:'Expense',
            projectId:null,
            approvers:[],
            approverId:null,
            users:[],
            UserId:null,
            fromDate:null,
            toDate:null,
            attrs: [
                {
                    key: 'today',
                    bar: true,
                    dates: new Date(),
                },
            ],
            reportPath:null,
            parameter:[]
        }
    },
    created:async function(){
        await this.GetProjectByCompanyIdAndUserId();
        await this.GetApproverListing()
        await this.GetUserListing();
    },
    methods: {
        createStringArray(arr, prop) {
            var result = [];
            for (var i = 0; i < arr.length; i += 1) {
                result.push(arr[i][prop]);
            }
            return result.toString();
        },
        onSearch: function () {
            debugger;
         var vm=this;
         var groupBy = $('input:radio[name=groupBy]:checked').val();
            var reportModel={
                reportName:'reimbursementreport',
                moduleName:'Expense',
                projectIds:vm.createStringArray(vm.projectId,'value'),
                userIds:vm.createStringArray(vm.UserId,'value'),
                fDate:vm.fromDate,
                approverIds:vm.createStringArray(vm.approverId,'value'),
                tDate:vm.toDate,
                groupByVal:groupBy
            }  
            var objectPostString = JSON.stringify(reportModel);     
            DataService.ReportMain(objectPostString).then(response => {                  
                     vm.reportPath=response.data.reportpath;
                     vm.parameter=response.data.parameters;
            });
        },
        onClear(){
            this.projectId=null;            
            this.UserId=null;
            this.approverId=null;
            this.fromDate=null;
            this.toDate=null;
        },
      async  GetProjectByCompanyIdAndUserId() {
          
          var vm = this;
          await DataService.GetProjectByCompanyIdAndUserId().then(function (response) {
          vm.projects = response.data.map((item) => {
          return { value: item.projectId, label: item.projectName };
        });
             
          });
      },
     
     GetApproverListing:async function(){
            let params = {};
            var vm=this;
            params["IsEncrptedId"] = '';
            params["moduleName"] = 'Expenses';
           await DataService.ExpenseGetUserListing(params).then(function (response) {
                vm.approvers =  response.data;
            });
    },
    GetUserListing:async function(){
            let params = {};
            var vm=this;
            params["IsEncrptedId"] = '';
            params["moduleName"] = 'Project';
           await DataService.ExpenseGetUserListing(params).then(function (response) {
                vm.users =  response.data;
            });
    },
}
}
</script>